import { useEffect, useRef } from 'react';
import { InnerBlocks } from '@wordpress/block-editor';

import { tabController } from '../../Components/utils/functions';

import Settings from './Settings';
import Style from './Style';

const Edit = props => {
	const { className, attributes, setAttributes, clientId, isSelected } = props;
	const { video, poster } = attributes;

	useEffect(() => { clientId && setAttributes({ cId: clientId.substring(0, 10) }); }, [clientId]); // Set & Update clientId to cId

	useEffect(() => tabController(), [isSelected]);

	const vbbVideoPlayer = useRef(null)

	useEffect(() => {
		vbbVideoPlayer.current.load();
	}, [video?.url])

	return <>
		<Settings attributes={attributes} setAttributes={setAttributes} clientId={clientId} />

		<div className={className} id={`vbbVideoBG-${clientId}`}>
			<Style attributes={attributes} clientId={clientId} />

			<video autoPlay muted loop playsinline className='vbbVideoPlayer' ref={vbbVideoPlayer} poster={poster?.url}>
				<source src={video?.url} type='video/mp4' />

				Your browser does not support HTML5 video.
			</video>

			<div className='vbbVideoOverlay'></div>

			<div className='vbbVideoContent'>
				<InnerBlocks template={[
					['core/heading', {
						content: 'Video Background by bPlugins',
						style: {
							typography: { fontSize: '40px' },
							color: { text: '#fff' }
						}
					}],

					['core/paragraph', {
						content: 'Lorem ipsum dolor sit amet consectetur adipisicing elit. Repellat, enim. Magni quis et voluptas voluptatum facere saepe cum reiciendis assumenda eveniet rem. Animi illo, doloribus suscipit, minus quos architecto eius repudiandae dignissimos voluptatibus facilis a quia odit voluptatem perspiciatis sit.',
						style: {
							typography: { fontSize: '20px' },
							color: { text: '#fff' }
						}
					}],

					['core/buttons', { contentJustification: 'center', layout: { justifyContent: 'center' } }, [
						['core/button', {
							text: 'Click Me',
							style: {
								typography: { fontSize: '30px' },
								color: { text: '#fff', gradient: 'linear-gradient(135deg, #4527a4, #8344c5)' }
							}
						}]
					]]
				]} />
			</div>
		</div >
	</>;
};
export default Edit;