import { getBackgroundCSS, getSpaceCSS } from '../../Components/utils/getCSS';

const Style = ({ attributes, clientId }) => {
	const { verticalAlign, textAlign, bgOverlay, minHeight, padding } = attributes;

	const mainSl = `#vbbVideoBG-${clientId}`;

	return <style dangerouslySetInnerHTML={{
		__html: `
		${mainSl}{
			min-height: ${minHeight};
		}
		${mainSl} .vbbVideoContent{
			justify-content: ${verticalAlign};
			text-align: ${textAlign};
			min-height: ${minHeight};
			padding: ${getSpaceCSS(padding)};
		}
		${mainSl} .vbbVideoOverlay{
			${getBackgroundCSS(bgOverlay)}
		}
		`.replace(/\s+/g, ' ')
	}} />
}
export default Style;