import { __ } from '@wordpress/i18n';
import { InspectorControls, BlockControls, AlignmentToolbar } from '@wordpress/block-editor';
import { PanelBody, __experimentalUnitControl as UnitControl } from '@wordpress/components';

// Settings Components
import { Label, Background, HelpPanel, InlineMediaUpload, SpaceControl } from '../../Components';
import { pxUnit, perUnit, emUnit } from '../../Components/utils/options';

import { verticalTopIcon, verticalCenterIcon, verticalBottomIcon } from './utils/icons';

const Settings = ({ attributes, setAttributes }) => {
	const { video, poster, verticalAlign, textAlign, bgOverlay, minHeight, padding } = attributes;

	return <>
		<InspectorControls>
			<div className='vbbInspectorInfo'>
				Need more block like this? Checkout the bundle ➡ <a href='https://wordpress.org/plugins/b-blocks' target='_blank' rel='noopener noreferrer'>B Blocks</a>
			</div>

			<HelpPanel slug='video-background-block' docsLink='https://bblockswp.com/docs/video-background-block' />


			<PanelBody className='bPlPanelBody' title={__('Video Background', 'video-background')}>
				<Label className='mb5'>{__('Background Video', 'video-background')}</Label>
				<InlineMediaUpload value={video?.url} types={['video']} onChange={val => setAttributes({ video: { id: null, url: val, alt: '', title: '' } })} placeholder={__('Enter Video URL', 'video-background')} />

				<Label>{__('Poster (Fallback Image)', 'video-background')}</Label>
				<InlineMediaUpload value={poster?.url} types={['image']} onChange={val => setAttributes({ poster: { id: null, url: val, alt: '', title: '' } })} placeholder={__('Enter image URL', 'video-background')} />

				<Background className='mt20' label={__('Background Overlay', 'video-background')} value={bgOverlay} onChange={val => setAttributes({ bgOverlay: val })} isImage={false} />

				<UnitControl className='mt20' label={__('Min Height:', 'video-background')} labelPosition='left' value={minHeight} onChange={val => setAttributes({ minHeight: val })} units={[pxUnit(), perUnit(), emUnit()]} />

				<SpaceControl className='mt20' label={__('Padding:', 'video-background')} value={padding} onChange={val => setAttributes({ padding: val })} defaults={{ vertical: '20px', horizontal: '30px' }} />
			</PanelBody>
		</InspectorControls>


		<BlockControls>
			<AlignmentToolbar value={verticalAlign} onChange={val => setAttributes({ verticalAlign: val })} describedBy={__('Vertical Alignment')} alignmentControls={[
				{ title: __('Content in top', 'video-background'), align: 'flex-start', icon: verticalTopIcon },
				{ title: __('Content in center', 'video-background'), align: 'center', icon: verticalCenterIcon },
				{ title: __('Content in bottom', 'video-background'), align: 'flex-end', icon: verticalBottomIcon }
			]} />

			<AlignmentToolbar value={textAlign} onChange={val => setAttributes({ textAlign: val })} />
		</BlockControls>
	</>;
};
export default Settings;